.document .header {
  background: #fff;
  position: fixed;
  width: 100%;
  z-index: 20;
}

.document .left-panel {
  position: fixed;
  left: 50px;
}

.document .left-panel .pdf-viewer {
  width: 250px;
  height: 407px;
  margin-top: 1em;
}

.document .content {
  margin-top: 150px;
  display: flex;
  justify-content: center;
}

.document .editor-container {
  position: fixed;
  right: 50px;
}

.document .content .main {
  padding: 24px;
  min-height: 90%;
  background: #fff;
}

.document .footer {
  text-align: center;
}
